:root
{
  --light-color: rgb(60, 58, 71);
  --color: rgb(37, 35, 44);
  --dark-color: rgb(18, 17, 22);
}

html, body, .app
{  
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
}

.app
{
  display: flex;
  flex-direction: column;
}

header
{
  background: var(--dark-color);
  display: flex;
  align-items: center;
  padding: 25px 0;
}

.button
{
  background: var(--light-color);
  color: white;  
  padding: 15px 20px;
  font-size: 25px;
  border: none;
  outline: none;
  border-radius: 10px;
  transition: background 0.1s;
}

.button:hover
{
  background: rgb(90, 87, 109);
}

.header-button
{
  margin: 0 15px;
}

.header-spacer
{
  flex: 1;
}

.file-dropdown
{
  background: var(--dark-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 120px;
  left: 10px;
  padding: 15px 20px;
  opacity: 0;
  z-index: 100;
  border-radius: 10px;
  pointer-events: none;
  transition: opacity 0.15 ease-out;
}

.file-dropdown-button p
{
  margin: 0;
}

.file-dropdown-item
{
  margin: 10px 0;
  width: 100%;
}

.file-dropdown-button:focus-within .file-dropdown
{ 
  opacity: 1;
  pointer-events: auto;
}

input
{
  background: var(--light-color);
  color: white;
  width: 250px;
  height: 50px;
  margin-left: 15px;
  padding: 0 10px;
  font-size: 25px;
  border: none;
  outline: none;
  border-radius: 10px;
}

.tabs
{
  background: var(--dark-color);
  display: flex;
  height: 60px;
}

.tab
{
  background: var(--color);
  color: white;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 200px;
  height: 100%;
  border: none;
  outline: none;
  cursor: pointer;
}

.tab-name
{
  margin: 0;
  font-size: 26px;
  font-family: sans-serif;
  text-align: center;
}

.close-tab-button
{
  background: none;
  color: gray;
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  font-size: 25px;
  font-family: sans-serif;
  border: none;
  outline: none;
  cursor: pointer;
  transition: color 0.1s;
}

.close-tab-button:hover
{
  color: white;
}

.main-section
{
  display: flex;
  flex-direction: column;
  height: 100%;
}

.horizontal-main-section
{
  display: flex;
  flex: 1.25;
}

.editor-container
{
  display: flex;
  flex-direction: column;
  width: 100%;
}

.editor 
{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.CodeMirror
{
  flex-grow: 1;
  font-size: 25px;
  text-indent: 10px;
}

.output
{
  background: rgb(67, 65, 82);
  color: white;
  width: 750px;
  padding: 10px 15px 0 15px;
  font-size: 30px;
  font-family: sans-serif;
}

.output-text
{
  color: gray;
  margin: 5px;
}

.website-output
{
  flex: 1;  
  border-top: 8px solid var(--dark-color);
}

.status-bar
{
  background: var(--dark-color);
  position: relative;
  display: flex;
  align-items: center;
  height: 75px;
}

.current-file
{
  position: absolute;
  color: gray;
  left: 15px;
  font-size: 25px;
  text-align: left;
  font-family: sans-serif;
}

.status-bar-spacer
{
  flex: 1;
}

.toggle-iframe-button
{
  margin-right: 10px;
  font-size: 22px;
  padding: 8px 20px;
}

.help-modal-background
{
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 100;  
}

.help-modal
{
  background: rgb(85, 85, 85);
  position: relative;
  display: flex;
  flex-direction: column;  
  width: 40%;
  margin: 300px auto;
  border-radius: 20px;
  overflow: hidden;
}

.help-modal-title
{
  background: rgb(37, 37, 37);
  color: white;
  width: 100%;
  margin: 0;
  padding: 20px 0;
  font-size: 50px;
  font-family: sans-serif;
  text-align: center;
}

.close-help-modal
{
  background: none;
  color: white;
  position: absolute;
  width: 65px;
  height: 65px;
  top: 5px;
  right: 5px;
  margin: 0;
  padding: 0;
  font-size: 35px;
  font-family: sans-serif;
  border: none;
  outline: none;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

.close-help-modal:hover
{
  transform:rotate(90deg);
}

.help
{
  color: white;
  margin: 40px 20px;
  font-size: 30px;
  font-family: sans-serif;
  text-align: center;
}